import React, { useContext } from 'react';
import { ContextFavoritos } from '../../contexto/ContextFavoritos';
import { ContextTema } from '../../contexto/ContextTema';
import { regex } from '../../utils/utils';
import versus from '../../img/versus.png'
import './comparacao.css'

function Comparacao() {
    const contextoTema = useContext(ContextTema)
    const contextoFavoritos = useContext(ContextFavoritos)
    const comparacao = JSON.parse(localStorage.getItem('listaComparacao'))
    const favoritos = JSON.parse(localStorage.getItem('favoritos'))
    const setSpan = contextoFavoritos.setSpan

    function getVeiculoByCodigo(obj) {
        let veiculoCompleto = ''
        favoritos.forEach(favorito => {
            const objfav = { codigo: favorito.CodigoFipe, ano: favorito.AnoModelo.toString() }
            if (JSON.stringify(obj) === JSON.stringify(objfav)) {
                veiculoCompleto = favorito
            }
        })
        return veiculoCompleto
    }
    const img = versus
    let tema = 'comparacao-container'
    const veiculo1 = getVeiculoByCodigo(comparacao[0])
    const veiculo2 = getVeiculoByCodigo(comparacao[1])
    if (contextoTema.tema) tema = 'comparacao-container compararaoClaro'
    const valor1 = parseInt(veiculo1.Valor.replace(/[^\d]/g, ''))
    const valor2 = parseInt(veiculo2.Valor.replace(/[^\d]/g, ''))
    const veiculoMaisBarato = valor1 < valor2 ? veiculo1 : veiculo2
    let diferenca = Math.abs(valor1 - valor2)
    diferenca = regex(diferenca)

    return (
        <span className="comparacao" data-span>
            <div className={tema}>
                <button className="comparacao-botao--fechar" data-botaofechar onClick={() => { setSpan(false) }}><i className="fas fa-times"></i></button>
                <div className="comparacao-div">
                    <ul className="comparacao-lista">
                        <li className="comparacao-item"><h4>Modelo: {veiculo1.Modelo}</h4></li>
                        <li className="comparacao-item"><h4>Ano: {veiculo1.AnoModelo}</h4></li>
                        <li className="comparacao-item"><h4>Marca: {veiculo1.Marca}</h4></li>
                        <li className="comparacao-item"><h4>Combustivel: {veiculo1.Combustivel}</h4></li>
                        <li className="comparacao-item"><h4>Preço: {veiculo1.Valor}</h4></li>
                    </ul>
                    <img src={img} alt="versus" className="comparacao-x" />
                    <ul className="comparacao-lista">
                        <li className="comparacao-item"><h4>Modelo: {veiculo2.Modelo}</h4></li>
                        <li className="comparacao-item"><h4>Ano: {veiculo2.AnoModelo}</h4></li>
                        <li className="comparacao-item"><h4>Marca: {veiculo2.Marca}</h4></li>
                        <li className="comparacao-item"><h4>Combustivel: {veiculo2.Combustivel}</h4></li>
                        <li className="comparacao-item"><h4>Preço: {veiculo2.Valor}</h4></li>
                    </ul>
                </div>
                <div className="comparacao-resultado">
                    <h2>O veiculo: {veiculoMaisBarato.Modelo} <br />Ano: {veiculoMaisBarato.AnoModelo} é <br /><span className="barato">R$ {diferenca}</span> mais barato.</h2>
                </div>
            </div>
        </span>
    )
}

export default Comparacao;