const urlBase = 'https://parallelum.com.br/fipe/api/v1/';

function pegarMarcas(tipo, next) {
    fetch(urlBase + tipo + '/marcas')
        .then(resp => {
            return resp.json()
        })
        .then(data => {
            next(data)
        })
}
function pegarModelos(tipo, marca, next) {
    fetch(urlBase + tipo + '/marcas/' + marca + '/modelos')
        .then(resp => {
            return resp.json()
        })
        .then(data => {
            next(data)
        })
}
function pegarAno(tipo, marca, modelo, next) {
    fetch(urlBase + tipo + '/marcas/' + marca + '/modelos/' + modelo + '/anos')
        .then(resp => {
            return resp.json()
        })
        .then(data => {
            next(data)
        })
}
function pegarPreco(tipo, marca, modelo, ano, next) {
    fetch(urlBase + tipo + '/marcas/' + marca + '/modelos/' + modelo + '/anos/' + ano)
        .then(resp => {
            return resp.json()
        })
        .then(data => {
            const busca = { tipo, marca, modelo, ano }
            const veiculo = { ...data, ...busca }
            next(veiculo)
        })
}
function atualizarPrecos(array, next) {
    const promessas = array.map(item => {
        return fetch(urlBase + item.tipo + '/marcas/' + item.marca + '/modelos/' + item.modelo + '/anos/' + item.ano)
            .then(resp => resp.json())
            .then(data => {
                const busca = { tipo: item.tipo, marca: item.marca, modelo: item.modelo, ano: item.ano }
                const veiculo = { ...data, ...busca }
                return veiculo
            })
    })
    Promise.all(promessas)
        .then(resp => {
            next(resp)
        })
        .catch(e => {
            console.log(e)
        })
}

function regex(preco) {
    let valor = preco.toString()
    if (valor.length <= 2) {
        return valor.replace(/(\d{1,2})/, '0,$1')
    } else if (valor.length === 3) {
        return valor.replace(/(\d{1,3})(\d{2})/, '$1,$2')
    } else if (valor.length <= 5) {
        valor = valor.replace(/^0/, '')
        return valor.replace(/(\d{1,3})(\d{2})/, '$1,$2')
    } else if (valor.length <= 8) {
        valor = valor.replace(/^0/, '')
        return valor.replace(/(\d{1,3})(\d{3})(\d{2})/, '$1.$2,$3')
    } else if (valor.length <= 11) {
        valor = valor.replace(/^0/, '')
        return valor.replace(/(\d{1,3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3,$4')
    } else if (valor.length <= 14) {
        valor = valor.replace(/^0/, '')
        return valor.replace(/(\d{1,3})(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3.$4,$5')
    }
}

export { atualizarPrecos, pegarPreco, pegarAno, pegarModelos, pegarMarcas, regex }