import React, { useState } from 'react';
import Favoritos from '../favoritos/favoritos';
import Veiculos from '../veiculos/veiculos';
import Comparacao from '../comparacao/comparacao';
import { ContextFavoritos } from '../../contexto/ContextFavoritos';
import './main.css'

function Main() {
    const [span, setSpan] = useState(false)

    function corpo() {
        if (!span) {
            return (
                <main>
                    <div className="container conteudo">
                        <ContextFavoritos.Provider value={{ funcao: () => { }, setSpan: (bool) => { setSpan(bool) } }}>
                            <Favoritos />
                            <Veiculos />
                        </ContextFavoritos.Provider>
                    </div>
                </main>
            )
        } else {
            return (
                <ContextFavoritos.Provider value={{ funcao: () => { }, setSpan: (bool) => { setSpan(bool) } }}>
                    <Favoritos />
                    <Veiculos />
                    <Comparacao />
                </ContextFavoritos.Provider>
            )
        }
    }

    return (
        corpo()
    );
}

export default Main;