import React, { useEffect, useState } from 'react';
import Footer from '../footer/footer';
import Header from "../header/header";
import Main from '../main/main';
import { ContextTema } from '../../contexto/ContextTema';
import { atualizarPrecos } from '../../utils/utils';
import './principal.css'

function Principal() {
    const [tema, setTema] = useState(false)
    let corFundo = ''

    getLocalStorage()
    function getLocalStorage() {
        const resultado = JSON.parse(localStorage.getItem('tema')) || false
        if (resultado !== tema) {
            setTema(resultado)
        }
    }

    fundo()
    function fundo() {
        if (tema) {
            corFundo = 'fundoBranco'
        } else {
            corFundo = ''
        }
    }

    useEffect(() => {
        const favoritos = JSON.parse(localStorage.getItem('favoritos')) || []
        if (favoritos.length > 0) {
            const array = []
            favoritos.forEach(favorito => {
                const obj = { tipo: favorito.tipo, marca: favorito.marca, modelo: favorito.modelo, ano: favorito.ano }
                array.push(obj)
            })
            atualizarPrecos(array, inserirNosFavoritos)

            function inserirNosFavoritos(favoritosAtualizados) {
                localStorage.setItem('favoritos', JSON.stringify(favoritosAtualizados))
            }
        }
    })

    return (
        <div className={corFundo}>
            <ContextTema.Provider value={{ tema, setTema: (tema) => { setTema(tema) } }}>
                <Header />
                <Main />
                <Footer />
            </ContextTema.Provider>
        </div>
    );
}

export default Principal;
